<template>
   <v-bottom-nav
      :active.sync="bottomNav"
      :value="true"
      color="#1e1e1e"
    >
      <v-btn
        color="#fff"
        value="filter"
        flat
        @click="leftAction"
      >
        <span>{{ isListView ? "Filter" : "Contact"}}</span>
        <v-icon >{{ isListView ? "filter_list" : "message"}}</v-icon>
      </v-btn>

      <v-btn
        color="#fb7600"
        flat
        value="switch"
        @click="switchView"
      >
        <span>{{isListDisplayed ? "Go To Map" : isListView ? "Back To List" : "Back To Details"}}</span>
        <v-icon >{{isListDisplayed ? "map" : "arrow_back"}}</v-icon>
      </v-btn>

      <v-btn
        color="#fff"
        value="sort"
        flat
        @click="rightAction"
      >
        <span>{{ isListView ? "Sort" : "Share" }}</span>
        <v-icon >{{ isListView ? "sort" : "share" }}</v-icon>
      </v-btn>
    </v-bottom-nav>
</template>

<script>
  import store from "@/store";
  import { mapState } from "vuex";
  import { SHOW_SORT_BAR, HIDE_SORT_BAR } from "../vuex/mutations";

  export default {
    data() {
      return {
        bottomNav: "switch"
      };
    },
    computed: {
      ...mapState({
        isSortBarDisplayed: state => state.announcements.isSortBarDisplayed
      }),
      isListView() {
        return this.$route.name === "flats" || this.$route.name === "rooms";
      }
    },
    props: {
      isListDisplayed: Boolean,
      switchView: Function,
      switchFilters: Function,
      hideFilters: Function,
    },
    methods: {
      leftAction() {
        if(this.isListView){
          store.commit(HIDE_SORT_BAR) 
          this.switchFilters() 
        }
      },
      rightAction() {
        if (this.isListView) {
          this.hideFilters()
          this.isSortBarDisplayed ? store.commit(HIDE_SORT_BAR) : store.commit(SHOW_SORT_BAR)
        }
      }
    }
  };
</script>

<style lang="sass" scoped>
    .v-bottom-nav
      height: 12% !important
</style>
