import { FETCH_ANNOUNCMENTS_WITH_FILTER, POST_FAVORITE } from "./actions";
import { FETCH_ANNOUNCMENTS } from "../scenes/List/vuex/actions"
import { SET_ANNOUNCMENTS, SET_FILTER, FILTER_TYPE, SET_FAVORITE, SORT, SHOW_FILTER_BAR, SHOW_SORT_BAR, HIDE_FILTER_BAR, HIDE_SORT_BAR } from "./mutations";
import { SET_CURRENT_ANNOUNCMENT_ID } from "../scenes/List/vuex/mutations"

import { fetchFlatsRequest, postFavoriteRequest } from "../services/api";

import {
    START_ASYNC_TASK,
    FINISH_ASYNC_TASK
} from "@/vuex/mutations";

const announcementsModule = {
    state: {
        announcements: [],
        sortType: "default",
        isSortBarDisplayed: false,
        isFilterBarDisplayed: false,
        priceRange: [600, 3200],
        selectedPriceRange: [600, 3200],
        areaRange: [20, 80],
        selectedAreaRange: [20, 80],
        roomsCounts: ["1", "2", "3", "4+"],
        selectedRoomsCount: 2,
        cities: ['Warszawa', 'Kraków', 'Białystok', 'Lublin'],
        selectedCity: 'Warszawa',
        currentAnnouncementId: '',
    },
    actions: {
        [FETCH_ANNOUNCMENTS]: async ({ commit, state }) => {
            commit(START_ASYNC_TASK);
            try {
                const { data } = await fetchFlatsRequest();
                console.warn(data)
                if (data.success) {
                    const announcements = data.announcements;
                    commit(SET_ANNOUNCMENTS, { announcements });
                }
            } catch (e) { }
            commit(FINISH_ASYNC_TASK);
        },
        [FETCH_ANNOUNCMENTS_WITH_FILTER]: async ({ dispatch, commit }, payload) => {
            commit(SET_FILTER, { filterType: payload.filterType, filterValue: payload.filterValue })
            dispatch(FETCH_ANNOUNCMENTS)
        },
        [POST_FAVORITE]: async ({ commit }, payload) => {
            commit(SET_FAVORITE, payload);
            try {
                const { data } = await postFavoriteRequest();
                if (data.success) {
                }
            } catch (e) { }
        }
    },
    mutations: {
        [SET_ANNOUNCMENTS]: (state, { announcements }) => {
            state.announcements = state.sortType === "default" ? sortByType(announcements, state.sortType) : announcements
        },
        [SET_FILTER]: (state, { filterType, filterValue }) => {
            switch (filterType) {
                case FILTER_TYPE.CITY:
                    state.city = filterValue;
                    break;
                case FILTER_TYPE.PRICE:
                    state.price = filterValue;
                    break;
                case FILTER_TYPE.AREA:
                    state.area = filterValue;
                    break;
                case FILTER_TYPE.ROOMS:
                    state.roomsCount = filterValue;
                    break;
            }
        },
        [SET_FAVORITE]: (state, { id }) => {
            let ann = state.announcements.find(ann => ann.id === id)
            if (ann) {
                ann.isFavorite = !ann.isFavorite
            }
        },
        [SET_CURRENT_ANNOUNCMENT_ID]: (state, { id }) => {
            state.currentAnnouncementId = id
        },
        [SORT]: (state, { type }) => {
            state.sortType = type;
            state.announcements = sortByType(state.announcements, type)
        },
        [HIDE_SORT_BAR]: (state) => {
            state.isSortBarDisplayed = false
        },
        [SHOW_SORT_BAR]: (state) => {
            state.isSortBarDisplayed = true
        }
    }
};

function sortByType(announcements, type) {
    switch (type) {
        case "default":
            return announcements.sort(() => Math.random() - 0.5)
        case "priceUP":
            return announcements.sort((a, b) => a.price - b.price)
        case "priceDOWN":
            return announcements.sort((a, b) => b.price - a.price)
        case "areaUP":
            return announcements.sort((a, b) => a.area - b.area)
        case "areaDOWN":
            return announcements.sort((a, b) => b.area - a.area)
        default:
            return announcements
    }
}

export default announcementsModule;
