<template>
  <div class="sort-toolbar">
    <v-icon class="close hidden-xs-only" @click="hideSortBar" color="#1c1c1c">keyboard_arrow_left</v-icon>
    <v-radio-group class="custom-radio-group" v-model="selectedType">
      <div class="type-group">
        <v-radio
          color="#fb7600"
          value="priceUP">
          <div class="const-label" slot="label">By price<v-icon>arrow_upward</v-icon></div>
        </v-radio>
        <v-radio
          color="#fb7600"
          value="priceDOWN">
          <div class="const-label" slot="label">By price <v-icon>arrow_downward</v-icon></div>
        </v-radio>
      </div>
      <div class="type-group">
        <v-radio
          color="#fb7600"
          label="By area UP"
          value="areaUP">
          <div class="const-label" slot="label">By area <v-icon>arrow_upward</v-icon></div>
        </v-radio>
        <v-radio
          color="#fb7600"
          value="areaDOWN">
          <div class="const-label" slot="label">By area <v-icon>arrow_downward</v-icon></div>
        </v-radio>
      </div>
      <div class="def-container">
          <v-radio
            color="#fb7600"
            label="Default"
            value="default"/>
      </div>
    </v-radio-group>
  </div>
</template>

<script>
  import store from "@/store";
  import {SORT, HIDE_SORT_BAR} from "../vuex/mutations"
  export default {
    computed: {
      selectedType: {
        get() {
          return store.state.announcements.sortType;
        },
        set(val) {
          store.commit(HIDE_SORT_BAR)   
          store.commit(SORT, {type: val})
        }
      }
    },
    methods: {
      hideSortBar(){
        store.commit(HIDE_SORT_BAR)
      }
    }
  };
</script>

<style lang="sass">
.sort-toolbar
  display: flex
  width: 100vw
  position: relative
  font-weight: bold
  flex-direction: row
  justify-content: center
  background: #fff
  @media screen and (max-width: 1050px)
    flex-direction: column
    align-items: center
  @media screen and (max-width: 600px)
    height: 84vh
  .v-input--radio-group--column .v-input--radio-group__input
    @media screen and (min-width: 1050px)
      flex-direction: row
      justify-content: center
  .close
    position: absolute
    top: 10px
    right: 50px
    cursor: pointer
    z-index: 2
    font-size: 70px
    font-weight: bold
    user-select: none
    transform: rotate(90deg)
  .custom-radio-group
    @media screen and (max-width: 600px)
      align-items: center
  .v-icon
    font-weight: bold
  .const-label
    width: 90px
    display: flex
    justify-content: space-between
  .type-group
    display: flex
    width: 300px
    margin: 5px
  .def-container
    display: flex
    margin: 5px
    justify-content: center
</style>
